html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-size: 16px; }

html,
body {
  height: 100%;
  min-height: 100%;
  /* for firefox */
  width: 100%; }

body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.EmptyLoading {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #787878; }
  .EmptyLoading h3 {
    margin-bottom: 0;
    color: #23995c; }

#SvgIconEmpty {
  width: 96px;
  fill: #aaaaaa; }

#SvgIconLoading {
  width: 72px;
  fill: #23995c;
  animation: spin1 1s infinite linear; }

#SvgIconError {
  fill: #aaaaaa;
  width: 96px; }
