html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-size: 16px; }

html,
body {
  height: 100%;
  min-height: 100%;
  /* for firefox */
  width: 100%; }

body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@-webkit-keyframes resize {
  0%,
  17.5% {
    width: 0; } }

@keyframes resize {
  0%,
  17.5% {
    width: 0; } }

td.star {
  width: 36px; }

td.title {
  width: 300px; }

td.body a {
  color: #1d5796; }

td.body ul {
  padding-left: 20px; }

.favButton {
  all: unset;
  cursor: pointer;
  -webkit-transition: 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49);
  transition: 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49);
  border-radius: 99px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .favButton svg {
    width: 24px;
    z-index: 5; }
  .favButton .starIcon {
    fill: #aaaaaa; }
  .favButton:hover, .favButton:focus {
    background-color: rgba(67, 204, 133, 0.2); }
    .favButton:hover .starIcon, .favButton:focus .starIcon {
      fill: none;
      stroke: #43cc85;
      stroke-width: 2px; }
  .favButton.favourited svg {
    will-change: width;
    -webkit-animation: resize 0.15s cubic-bezier(0.17, 0.89, 0.32, 1.49);
            animation: resize 0.15s cubic-bezier(0.17, 0.89, 0.32, 1.49); }
  .favButton.favourited .starIcon {
    fill: #23995c;
    stroke: none; }
